import React, { useEffect } from 'react';

function ContextMenu(props){

    const menuId = props.targetId + "_contextMenu";
    const zoomId = props.targetId + "_zoomed";

    const onMouseLeave = () => {
        props.deactivateContext();
    }

    useEffect(() => {
        let contextMenuElement = document.getElementById(menuId);
        contextMenuElement.addEventListener("mouseleave", onMouseLeave);

        let zoomElement = document.getElementById(zoomId);
        zoomElement.addEventListener("mouseleave", deactivateZoom);

        return () => {
            contextMenuElement.removeEventListener("mouseleave", onMouseLeave);
            zoomElement.removeEventListener("mouseleave", deactivateZoom);
        }
    });

    const activateZoom = () => {
        props.onActivateZoom();
        props.onClose();
    }

    const deactivateZoom = () => {
        props.onDeactivateZoom();
    }

    const zoneMovementButtons = props.validMoveDestinations.map((x, idx) => {
        const onClick = () => {
            x.sendFunction();
            props.onClose();
        }
        return <button key={idx} onClick={onClick}>{x.displayName}</button>
    });

    return (
        <div id={menuId} className="right-click-context">
            <button onClick={activateZoom}>Magnify...</button>
            <div>{props.contextHeader}</div>
            {zoneMovementButtons}
            <br />
            <br />
            <button onClick={props.onClose}>Close</button>
        </div>
    )
}

export default ContextMenu;